import React, { useState, useEffect } from 'react';
import { Chart } from 'chart.js';
import { BarController, BarElement, LineController, LineElement, PointElement, LinearScale, CategoryScale } from 'chart.js';
import { Bar, Scatter } from 'react-chartjs-2';
import { getDatabase, ref, onValue } from "firebase/database";
import app from './firebaseConfig';
import './App.css';

Chart.register(BarController, BarElement, LineController, LineElement, PointElement, LinearScale, CategoryScale);

function App() {
    const [scatterPlotData, setScatterPlotData] = useState({
        labels: [],
        datasets: [{
            label: 'Ball Position Misses',
            data: [],
            borderColor: 'rgb(255,99,132)',
            backgroundColor: 'rgba(255,99,132,0.5)',
        }]
    });

    const [graphData, setGraphData] = useState({
        labels: [],
        datasets: [{
            label: 'Number of Misses',
            data: [],
            //borderColor: 'rgb(255, 99, 132)',
            backgroundColor: 'rgba(255, 99, 132, 0.5)',
        }]
    });
    
    useEffect(() => {
        const fetchData = () => {
            const db = getDatabase(app);
            const dataRef = ref(db);  // Pointing to the root of your database

            onValue(dataRef, (snapshot) => {
                const rawData = snapshot.val();
                console.log("Raw Data:", rawData);

                if (!rawData) {
                    console.log("No data found in Firebase");
                    return;
                }

                const aggregatedData = {};

                for (let key in rawData) {
                    if (rawData[key].ballToPaddleDistance && rawData[key].numMisses) {
                        const distanceRounded = Math.round(rawData[key].ballToPaddleDistance);  // Rounding to the nearest tenth

                        if (aggregatedData[distanceRounded]) {
                            aggregatedData[distanceRounded] += rawData[key].numMisses;
                        } else {
                            aggregatedData[distanceRounded] = rawData[key].numMisses;
                        }
                    }
                }

                console.log("Aggregated Data:", aggregatedData);
                const scatterDataPoints = [];

                for (let key in rawData) {
                    if (rawData[key].ball_position !== undefined && rawData[key].playerID !== undefined) {
                        const x = rawData[key].playerID === 1 ? 0 : 20;
                        const y = rawData[key].ball_position;
                        scatterDataPoints.push({ x, y });
                    }
                }
                const scatterData = {
                    labels: [],  // you can leave this empty or use timestamps, etc.
                    datasets: [{
                        label: 'Ball Position Misses',
                        data: scatterDataPoints,
                        borderColor: 'rgb(255, 99, 132)',
                        backgroundColor: 'rgba(255, 99, 132, 0.5)',
                    }]
                };

                setScatterPlotData(scatterData);

                const transformedData = {
                    labels: Object.keys(aggregatedData).sort((a, b) => parseFloat(a) - parseFloat(b)),  // Sorted labels
                    datasets: [
                        {
                            label: 'Number of Misses',
                            data: Object.values(aggregatedData),
                            //borderColor: 'rgb(255, 99, 132)',
                            backgroundColor: 'rgba(255, 99, 132, 0.5)',
                        },
                    ],
                };

                //console.log("Transformed Data:", transformedData);
                setGraphData(transformedData);
            });
        };

        fetchData();
    }, []);


    return (
        <div>
            <div className="chartContainer">
            {/* Scatter Plot Graph */}
            {scatterPlotData.datasets && scatterPlotData.datasets[0].data.length > 0 ? (
                <Scatter data={scatterPlotData}
                    options={{
                        plugins: {
                            title: {
                                display: true,
                                text: 'Ball Position vs Player ID'
                            },
                            legend: {
                                display: true
                            }
                        },
                        scales: {
                            x: {
                                type: 'linear',
                                position: 'bottom',
                                title: {
                                    display: true,
                                    text: 'Ball Position (X coordinate)'
                                }
                            },
                            y: {
                                title: {
                                    display: true,
                                    text: 'Ball Position (Y coordinate)'
                                }
                            }
                        }
                    }}
                />
                ) : <div>Loading Scatter Plot...</div>}
            </div>
            <div className="LineGraphContainer">
            {/* Line Graph */}
            {graphData.labels.length > 0 ? (
                <Bar key={Math.random()} data={graphData}
                    options={{
                        plugins: {
                            title: {
                                display: true,
                                text: 'Miss Frequency vs. Ball to Paddle Distance when Missed'
                            },
                            legend: {
                                display: true
                            }
                        },
                        scales: {
                            x: {
                                type: 'linear',
                                position: 'bottom',
                                title: {
                                    display: true,
                                    text: 'Distance (from ball to paddle)'
                                }
                            },
                            y: {
                                title: {
                                    display: true,
                                    text: 'Miss Frequency'
                                }
                            }
                        }
                    }}
                />
                ) : <div>Loading Bar Graph...</div>}
            </div>
        </div>
    );
}
export default App;